import { RPTooltip } from "@rp/react/web/common/base-components"
import { Button, Typography } from "@rp/react/web/mui/core"
import { HeaderStyled, classes } from "./HeaderButton.style"

type HeaderButtonProps = {
  children: React.ReactNode
  startIcon?: React.ReactNode
  icon?: React.ReactNode
  size?: "medium" | "large" | "small" | undefined
  testid?: string
  tooltip?: string
  hintText?: string
  disabled?: boolean
  variant?: "text" | "contained" | "outlined" | "contained-light" | undefined
  onClick?: () => void
}
const HeaderButton = ({
  children,
  icon,
  size = "small",
  testid,
  tooltip = "",
  variant = "contained",
  hintText,
  ...rest
}: HeaderButtonProps) => {
  const hasChildren = !!children
  return (
    <HeaderStyled className={classes.headerButtonContainer} size={size} hasChildren={hasChildren}>
      {!!hintText && <Typography className={classes.hintText}>Autosave enabled</Typography>}
      <RPTooltip title={tooltip}>
        <span style={{ height: "fit-content" }}>
          <Button
            classes={{
              root: classes.rootButton,
              startIcon: classes.startIcon,
            }}
            data-testid={testid}
            variant={variant}
            startIcon={icon}
            {...rest}
          >
            {children}
          </Button>
        </span>
      </RPTooltip>
    </HeaderStyled>
  )
}

export { HeaderButton }
